import { isLabelWithInternallyDisabledControl } from "@testing-library/user-event/dist/utils";
import React, { useState } from "react";
export default function About() {
  const [aboutMystyle, setMystyle] = useState({
    backgroundColor: "white",
    color: "black",
  });
  const [btnTet, setbtn] = useState("Enabling light mode");
  const clickMode = () => {
    if(aboutMystyle.color==='white'){
        setMystyle({
            backgroundColor: "white",
            color: "black",
        })
        setbtn("Enabling Dark mode")
    }
    else{
        setMystyle({
            backgroundColor: "black",
            color: "white",
            border: ".px solid #0000FF", 
        })
        setbtn("Enabling light mode")
    }
  };

  return (
    <div className="container my-4" style={aboutMystyle}>
        <h1 style={aboutMystyle}>About us</h1>
      <div className="accordion my-3 " id="accordionExample"style={aboutMystyle}>
        <div className="accordion-item my-2"style={aboutMystyle}>
          <h2 className="accordion-header  my-2 " style={aboutMystyle}id="headingOne">
            <button 
              className="accordion-button "style={aboutMystyle}
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseOne"
              aria-expanded="true"
              aria-controls="collapseOne"
            >
              Accordion Item #1
            </button>
          </h2>
          <div
            id="collapseOne"style={aboutMystyle}
            className="accordion-collapse collapse show"
            aria-labelledby="headingOne"
            data-bs-parent="#accordionExample"
          >
            <div className="accordion-body"style={aboutMystyle}>
              <strong>This is the first item's accordion body.</strong> It is
              shown by default, until the collapse plugin adds the appropriate
              classes that we use to style each element. These classes control
              the overall appearance, as well as the showing and hiding via CSS
              transitions. You can modify any of this with custom CSS or
              overriding our default variables. It's also worth noting that just
              about any HTML can go within the <code>.accordion-body</code>,
              though the transition does limit overflow.
            </div>
          </div>
        </div>
        <div className="accordion-item my-2"style={aboutMystyle}>
          <h2 className="accordion-header" id="headingTwo"style={aboutMystyle}>
            <button
              className="accordion-button collapsed"style={aboutMystyle}
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseTwo"
              aria-expanded="false"
              aria-controls="collapseTwo"
            >
              Accordion Item #2
            </button>
          </h2>
          <div
            id="collapseTwo"style={aboutMystyle}
            className="accordion-collapse collapse"
            aria-labelledby="headingTwo"
            data-bs-parent="#accordionExample"
          >
            <div className="accordion-body"style={aboutMystyle}>
              <strong>This is the second item's accordion body.</strong> It is
              hidden by default, until the collapse plugin adds the appropriate
              classes that we use to style each element. These classes control
              the overall appearance, as well as the showing and hiding via CSS
              transitions. You can modify any of this with custom CSS or
              overriding our default variables. It's also worth noting that just
              about any HTML can go within the <code>.accordion-body</code>,
              though the transition does limit overflow.
            </div>
          </div>
        </div>
        <div className="accordion-item my-2"style={aboutMystyle}>
          <h2 className="accordion-header" id="headingThree"style={aboutMystyle}>
            <button
              className="accordion-button collapsed"style={aboutMystyle}
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#collapseThree"
              aria-expanded="false"
              aria-controls="collapseThree"
            >
              Accordion Item #3
            </button>
          </h2>
          <div
            id="collapseThree"style={aboutMystyle}
            className="accordion-collapse collapse"
            aria-labelledby="headingThree"
            data-bs-parent="#accordionExample"
          >
            <div className="accordion-body"style={aboutMystyle}>
              <strong>This is the third item's accordion body.</strong> It is
              hidden by default, until the collapse plugin adds the appropriate
              classes that we use to style each element. These classes control
              the overall appearance, as well as the showing and hiding via CSS
              transitions. You can modify any of this with custom CSS or
              overriding our default variables. It's also worth noting that just
              about any HTML can go within the <code>.accordion-body</code>,
              though the transition does limit overflow.
            </div>
          </div>
        </div>
      </div>
      {/* <button className="btn btn-primary my-3"style={aboutMystyle} onClick={clickMode}>
        {btnTet}
      </button> */}
    </div>
  );
}
