// import React from 'react';
import './App.css';
import About from './components/About';
import Navbar from './components/Navbar';
import Form from './components/Form';
import Alert from './components/Alert';
import ReactDOM from 'react-dom';
import React, { useState } from 'react';
// import {BrowserRouter as  Routes,Router, Route} from 'react-router-dom';
import {BrowserRouter,Routes,Router, Route} from "react-router-dom";
function App() {
  const [mystyle, setmyStyle] = useState('light');
  const [writeMode, setwrite] = useState("Enable light Mode");
  // const [writeMode1,setwrite1]=useState("Enable red Mode");
  // const [writeMode2,setwrite2]=useState("Enable light Mode");
  // const [writeMode3,setwrite3]=useState("Enable light Mode");
  const [alert, setalert] = useState(null);
  const showalert = (msg, type) => {
    setalert({
      msg: msg,
      type: type,
    })
    setTimeout(() => {
      setalert(null);
    }, 1500);
  }
  const toggle = () => {
    if (mystyle === 'light') {
      document.body.style.backgroundColor = 'white';
      setwrite("Enable dark Mode")
      setmyStyle('dark');
      showalert("Light mode is enabled", "Success")
      document.title = "Enable- light mode";
    }
    else {
      document.body.style.backgroundColor = 'grey';
      setwrite("Enable light Mode")
      setmyStyle('light');
      showalert("Dark mode is enabled", "Success");
      document.title = "Enable- dark mode";
    }
  }
  const bColor = () => {
    if (mystyle === 'light') {
      document.body.style.backgroundColor = 'green'
      setmyStyle('dark');
      setwrite('Enable red mode');
      showalert("Green mode is enabled", "secondary")
      document.title = "Enable- green mode";
    }
    else {
      document.body.style.backgroundColor = 'red'
      setmyStyle('light')
      setwrite('Enable green mode');
      showalert("Green mode is enabled", "secondary")
      document.title = "Enable- red mode";
    }
  }
  const b1Color = () => {
    if (mystyle === 'light') {
      document.body.style.backgroundColor = 'aqua'
      setmyStyle('dark');
      setwrite('Enable purple mode');
      showalert("Green mode is enabled", "primary")
      document.title = "Enable- aqua mode";
    }
    else {
      document.body.style.backgroundColor = 'purple'
      setmyStyle('light')
      setwrite('Enable pink mode');
      showalert("Green mode is enabled", "primary")
      document.title = "Enable- purple mode";
    }
  }
  const b2Color = () => {
    if (mystyle === 'light') {
      document.body.style.backgroundColor = 'blue';
      setmyStyle('dark');
      setwrite('Enable yellow mode');
      showalert("Green mode is enabled", "warning")
      document.title = "Enable- blue mode";
    }
    else {
      document.body.style.backgroundColor = 'yellow'
      setmyStyle('light')
      setwrite('Enable blue mode');
      showalert("Green mode is enabled", "warning")
      document.title = "Enable- yellow mode";
    }
  }
  return (
   
    <>
  
      <BrowserRouter>
        <Navbar title="Rajveer" about="aboutUs" bColor={bColor} b1Color={b1Color} b2Color={b2Color} toggleMode={toggle} style={mystyle} write={writeMode} />
        <Alert alert={alert} />
       <Routes>
          <Route path="/about" element={ < About />}>
           
          </Route>
         
          <Route exact path="/" element={<Form heading="Login Form" showalert={showalert} alert={alert} />} >
            
          <Form heading="Login Form" showalert={showalert} alert={alert} />
              </Route>
        </Routes>
      </BrowserRouter>
     
      {/* <About/> */}
    </>
    
  );
  
}

export default App;
