import React from 'react'
import PropTypes from 'prop-types';
import { Link} from 'react-router-dom';
export default function Navbar(props) {
 
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-secondary">
        <div className="container-fluid">
          <link to="/">{props.title}</ link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link active" aria-current="page" to="/">Home</Link>
              </li>
              <li className="nav-item">
                <link Link  to="/about">{props.about}</link>
              </li>
            </ul>
            <form className="d-flex" role="search">
              <input className="form-control me-2" id='search' type="search" placeholder="Search" />
              <button className="btn btn-outline-success" type="submit">Search</button>
            </form>
          <div className={`form-check form-switch ${props.sytle==='light'?'light':'dark'}`}>
          <input className="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckDefault" onClick={props.bColor}/>
                <label className="form-check-label" htmlFor="flexSwitchCheckDefault">{props.write}</label>
            </div>
            <div className={`form-check form-switch ${props.sytle==='light'?'light':'dark'}`}>
          <input className="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckDefault" onClick={props.toggleMode}/>
                <label className="form-check-label" htmlFor="flexSwitchCheckDefault">{props.write}</label>
            </div>
            <div className={`form-check form-switch ${props.sytle==='light'?'light':'dark'}`}>
          <input className="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckDefault" onClick={props.b1Color}/>
                <label className="form-check-label" htmlFor="flexSwitchCheckDefault">{props.write}</label>
            </div>
            <div className={`form-check form-switch ${props.sytle==='light'?'light':'dark'}`}>
          <input className="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckDefault" onClick={props.b2Color}/>
                <label className="form-check-label" htmlFor="flexSwitchCheckDefault">{props.write}</label>
            </div>
          </div>
        </div>
      </nav>
    </>
  )
}
Navbar.propTypes = {
  //This tells that type is string but when we write isrequired with it then it should not be undefined 
  title: PropTypes.string,
  about: PropTypes.string.isRequired,
}
Navbar.defaultProps = {
  title: "sidhu",
  about: "services"
};
