import React, { useState } from 'react';

export default function Form(props) {
    const  onClickHandle=()=>{
        console.log("i clicked on it");
        //    setText("rajveer singh")
        setText("my name is rajveer singh")
        let netText=text.toUpperCase();
        setText(netText);
        props.showalert("converted into upper case","Success");
    }
    const del=()=>{
      setText("")
      props.showalert("text is deleted","Success");
    }
    const onClicklower=()=>{
let netText=text.toLowerCase();
setText(netText);
    }
    const onClickCopy=()=>{}

//  const copyToClipboard = (text) => {
//       console.log('text', text)
//       var textField = document.createElement('textarea')
//       textField.innerText = text
//       document.body.appendChild(textField)
//       textField.select()
//       document.execCommand('copy')
//       textField.remove()
//     }
    const handleOnChange=(event)=>{
        // console.log("onChange");
        setText(event.target.value)
    }
    if(document.body.style.backgroundColor==='black'){
      document.body.style.color='white';
    }
    else{
      document.body.style.color='black';
    }
    const [text, setText] = useState("");
    return (
      <>
    <div className='container'>
    <h1>{props.heading}</h1>
  <textarea className="Form-check-input" id="write" value={text} onChange={handleOnChange} rows="4" cols="134"/></div>
  <button className="btn btn-primary  "disabled={text.length===0 } style={{marginLeft:"82px"}}  onClick={onClickHandle}>Convert to upperCase</button>
  <button className="btn btn-primary  " disabled={text.length===0} style={{marginLeft:"12px"}}  onClick={onClicklower}>Convert to lowerCase</button>
  <button className="btn btn-primary  "disabled={text.length===0} style={{marginLeft:"12px"}}  onClick={del}>Delete text</button>
  <button className="btn btn-primary  "disabled={text.length===0} style={{marginLeft:"12px"}}  onClick={() => navigator.clipboard.writeText(text)}>copy Text</button>
  {/* <button className="btn btn-primary mx-5" onclick={copyToClipboard}> copy text

  </button> */}
    
    <div className="container">
<h3>Count the words</h3>
{/* to count the words and chect the length */}
<p>{text.split(" ").filter((element)=>{
  return element.length!==0}).length
}words and {text.length} charactes</p>
<h3>Preview</h3>
<p>{text}</p>
    </div>
    </>
  )
}
